"use client";

import levels from "./module/[id]/data/modules";
import Link from "next/link";
import ProgressBar from "../../../components/UI/ProgressBar";

const ProgressPage = () => {
  return (
    <div className="p-6 lg:p-8 flex flex-col gap-8 bg-background min-h-screen">
      <h1 className="text-2xl font-bold">Tu Progreso</h1>
      <p className="text-neutral-600">
        Aquí puedes ver tu progreso en cada módulo.
      </p>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {levels.map((level) => (
          <div key={level.id} className="p-4 border rounded-lg shadow-md bg-white">
            <h2 className="font-bold text-lg">{level.title}</h2>
            <p className="text-sm text-neutral-500">{level.subtitle}</p>
            
            {/* 🔹 Barra de progreso del nivel */}
            <ProgressBar progress={level.progress} />

            <Link
              href={`/module/${level.id}`}
              className="mt-2 inline-block text-primary hover:underline text-sm font-medium"
            >
              Ver módulos
            </Link>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ProgressPage;
